.page-main-image {
  justify-self: end;
  object-fit: cover;
  width: 100%;
  height: 340px;
  border-radius: 16px;
}

.menu-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

/* Global styles for each pages' components */


/* media queries */
@media screen and (max-width:840px){
  .menu-list {
      grid-template-columns: repeat(2,1fr)
  }
}

@media screen and (max-width: 620px) {
  .menu-list {
      grid-template-columns: 1fr;
  }
}